hr {
  background: #cbc6df;
  height: 1px;
  border: none;
  margin: 0;
}

.section-accordion,
.section-accordion-sections,
.sections-content {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.section-accordion-sections {
  overflow: hidden;
  transition: height 0.3s ease-in-out;
}

.accordion-header {
  display: flex;
  align-items: center;
  gap: 24px;
  background-color: transparent;
  width: 100%;
  padding: 0;
}
.accordion-header:hover h3 {
  color: #5c00dc;
}

.accordion-icon {
  width: 28px;
  height: 28px;
}
@media (min-width: 620px) {
  .accordion-icon {
    width: 35px;
    height: 35px;
  }
}